$('form a[data-show]').on('click', function (e) {
	var $el = $(e.currentTarget);

	if ($el.data('show')) {
		e.preventDefault();

		if (history.pushState && typeof history.pushState === 'function') {
			history.pushState({}, $el.attr('title'), $el.attr('href'));
		}

		$('#' + $el.data('hide')).fadeOut(100, function () {
			$('#' + $el.data('show')).fadeIn();
		});
	}
});

